<template>
  <div class="apanage_detail">
    <div class="box first">
      <div class="row">
        <div class="left">
          设备位置:
        </div>
        <div class="right">
          {{apanageDetail.devicePosition}}
        </div>
      </div>
    </div>
    <div class="box second">
      <div class="row">
        检查情况:
      </div>
      <div class="hr"></div>
      <div class="row">
        {{apanageDetail.check_state || '暂无'}}
      </div>
    </div>
    <div class="box third">
      <div class="row">
        发现问题:
      </div>
      <div class="hr"></div>
      <div class="row">
        {{apanageDetail.find_problem || '暂无'}}
      </div>
    </div>
    <div class="box fourth">
      <div class="row">
        备注:
      </div>
      <div class="hr"></div>
      <div class="row">
        {{apanageDetail.note || '暂无'}}
      </div>
    </div>
    <div class="box fifth">
      <div class="row">
        现场图片:
      </div>
      <div class="hr"></div>
      <div class="row">
        <div v-for="item in apanageDetail.imgs" :key="item.url" class="img_box">
          <img :src="item.url" alt="" v-preview="item.url">
        </div>
      </div>
    </div>
    <div class="box sixth">
      <div class="row">
        <div class="left">
          检查岗位:
        </div>
        <div class="right">
          {{apanageDetail.post_check}}
        </div>
      </div>
      <div class="hr"></div>
      <div class="row">
        <div class="left">
          参考依据:
        </div>
        <div class="right">
          {{apanageDetail.reference_according}}
        </div>
      </div>
      <div class="hr"></div>
      <div class="row">
        <div class="left">
          考核分值:
        </div>
        <div class="right">
          {{apanageDetail.summary_score}}
        </div>
      </div>
      <div class="hr"></div>
      <div class="row">
        <div class="left">
          检查时间:
        </div>
        <div class="right">
          {{apanageDetail.check_time}}
        </div>
      </div>
    </div>
    <div class="blank"></div>
  </div>
</template>

<script>
export default {
  name: 'ApanageDetail',
  data () {
    return {
      id: '',
      apanageDetail: {
        id: 1,
        check_state: '',
        find_problem: '',
        note: '',
        post_check: '',
        reference_according: '',
        summary_score: '',
        check_time: '',
        imgs: []
      }
    }
  },
  methods: {
    getApanageData (id) {
      this.$rpc.call('funenc_xa_station.belong_to_management', 'get_belong_to_management_by_id', [], {
        id: id
      }).then(res => {
        this.apanageDetail = res
        if (this.apanageDetail.imgs) {
          this.apanageDetail.imgs = JSON.parse(this.apanageDetail.imgs)
        }
      }).catch(e => {
        console.log(e)
        alert('获取规章制度失败')
      })
    }
  },
  computed: {
  },
  mounted () {
    this.id = this.$route.params.id
    this.getApanageData(this.id)
  }
}
</script>

<style lang="less" scoped>
@import '../../styles/common';
.apanage_detail{
  .box{
    margin-top: 16 * @px1;
    background: #fff;
    overflow: hidden;
    .row{
      padding-left: 16 * @px1;
      padding-right: 16 * @px1;
      line-height: 56 * @px1;
      height: 56 * @px1;
    }
  }
  .first,.sixth{
    .row{
      .right{
        text-align: right;
        color: @titleGray;
      }
    }
  }
  .second,.third,.fourth{
    .row:last-child{
      color: @titleGray;
      line-height: 22 * @px1;
      padding-top: 17 * @px1;
      padding-bottom: 17 * @px1;
      height: 100%;
    }
  }
  .fifth{
    .row:last-child{
      padding-top: 17 * @px1;
      padding-bottom: 17 * @px1;
      height: 100%;
      line-height: 0;
      overflow: hidden;
      display: flex;
      flex-wrap: wrap;
      justify-content: space-around;
      .img_box{
        margin-top: 20 * @px1;
        width: 100 * @px1;
        height: 100 * @px1;
        border: 1 * @px1 solid rgba(200,200,200, 0.6);
        text-align: center;
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 5 * @px1;
        box-sizing: border-box;
        img{
          max-width: 90 * @px1;
          max-height: 90 * @px1;
        }
      }
    }
  }

  .third{
    .row{
      .right{
        text-align: right;
        color: @titleGray;
      }
    }
  }
}
</style>
